import React from 'react';
import logo from './logo.svg';
import './App.css';
import {gql} from 'apollo-boost';
import {Query} from 'react-apollo';
import Show from './Show';
import {ApolloProvider} from 'react-apollo';
import {createHttpLink} from 'apollo-link-http';
import {InMemoryCache} from 'apollo-cache-inmemory';
import {ApolloClient} from 'apollo-boost';

var cache = new InMemoryCache();




var GET_DATA = gql`
	 query myPeople($page:Int!)
	 {
 myPeople(page:$page){
  data{
    
    full_name,email,accepted,id,contact_detail{phone}
  },
  
}
	 }
 `;
class App extends React.Component {
constructor(props) {
    super(props);
    this.state={
	page:1,
	fet:null,
	client : new ApolloClient({
link: createHttpLink({
	uri: ''
}),
cache
 })};
  }

render(){
console.log("Copyright (c) 2019 Mohamed Jerbi");
  return (<ApolloProvider client={this.state.client} >
  <p>Put Below The Page Number</p>
  <input value={this.state.page} onChange={(e)=>this.setState({page:parseInt(e.target.value, 10)})} />
    <Query query={GET_DATA} variables={{page:this.state.page}}>
	{({loading,error,data,refetch })=>{
	if(loading) return <p>loading</p>;
	if(error) return (<div>{console.log("please copy and report this error to Mohamed Jerbi"+error)}
	<p>Put Below Your Full Token URL(each token lifespam of 2hours)</p>
	<input value={this.state.val} onChange={(s)=>this.setState({val:s.target.value})}/>
	<button onClick={()=>this.setState({client:new ApolloClient({
link: createHttpLink({
	uri: this.state.val
}),
cache
 })
 })} >insert token</button>
	</div>);
	return (
	<Show data={data.myPeople} />
	)}
	
	
	}
	</Query></ApolloProvider>
  );}
}

export default App;
