
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
  root: {
    width: '100%',
    overflowX: 'auto',
  },
  table: {
    minWidth: 650,
  },
});


const Show=(props)=>{ 
  const classes = useStyles();

  return (
    <Paper className={classes.root}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
			<TableCell align="left">Phone</TableCell>
            <TableCell align="left">Email</TableCell>
            <TableCell align="left">Accepted</TableCell>
			<TableCell align="left">Expa Link</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {props.data.data.map(row => (
            <TableRow key={row.full_name}>
              <TableCell component="th" scope="row">
                {row.full_name}
              </TableCell>
			  <TableCell align="left">{row.contact_detail.phone}</TableCell>
              <TableCell align="left">{row.email}</TableCell>
              <TableCell align="left">{row.accepted? "true" : "false"}</TableCell>
			  <TableCell align="left"><a href={"https://expa.aiesec.org/people/"+row.id}>Expa Profile Link</a></TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Paper>
  );
}
export default Show;